.chatbot-chat-container {
    width: 100%;
    height: 100vh;
}

.chatbot-chat-content{
    width: 85%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: auto;
}

.chatbot-chat {
    width: 100%;
    height: 500px;
    background: #fff;
    border-radius: 15px;
    display: flex;
    flex-direction: column;
    box-shadow: 2px 2px 15px 0 rgba(0, 0, 0, .16);
    padding: 20px;
    box-sizing: border-box;
}

.chatbot-chat-containter-body {
    width: 100%;
    height: 90%;
    overflow: scroll;
}

.chatbot-chat-container-input{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
